.kt-radio {
  display: inline-block;
  position: relative;
  padding-left: 30px;
  text-align: left;
  margin-bottom: 10px;
  cursor: pointer;
  font-size: 1rem;
  -webkit-transition: all 0.3s ease;
  transition: all 0.3s ease;
}

.kt-radio.kt-radio--disabled {
  opacity: 0.8;
  cursor: not-allowed;
}

.kt-radio > input {
  position: absolute;
  z-index: -1;
  opacity: 0;
}

.kt-radio > span {
  background: none;
  position: absolute;
  top: 1px;
  left: 0;
  height: 18px;
  width: 18px;
  border-radius: 50% !important;
}

.kt-radio > span:after {
  content: '';
  position: absolute;
  display: none;
  top: 50%;
  left: 50%;
  margin-left: -3px;
  margin-top: -3px;
  width: 6px;
  height: 6px;
  border-radius: 100% !important;
}

.kt-radio > input:checked ~ span {
  -webkit-transition: all 0.3s ease;
  transition: all 0.3s ease;
  background: none;
}

.kt-radio > input:checked ~ span:after {
  display: block;
}

.kt-radio:hover > input:not([disabled]):checked ~ span,
.kt-radio > input:checked ~ span {
  -webkit-transition: all 0.3s ease;
  transition: all 0.3s ease;
}

.kt-radio > input:disabled ~ span {
  opacity: 0.6;
  pointer-events: none;
}

.kt-radio.kt-radio--solid > span {
  border: 1px solid transparent;
}

.kt-radio.kt-radio--solid:hover > input:not([disabled]) ~ span,
.kt-radio.kt-radio--solid > input:focus ~ span {
  -webkit-transition: all 0.3s ease;
  transition: all 0.3s ease;
